import React, { useState, useEffect } from 'react';
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import style from './timerStyles.module.css'
import TaimeSound from './timer-bell.mp3'
import { Howl } from 'howler';
import ModalVote from '../ModalVote/ModalVote';

function Taimer(props) {

  // const [seconds, setSeconds] = useState(50)
  const [day, setDay] = useState(true)
  const [night, setNight] = useState(false)
  const [dayVote, setDayVote] = useState(true)

  // useEffect(() => {
  //   const timer = setInterval(() => {
  //     setSeconds(seconds - 1)
  //     if (seconds === 1) {
  //       setSeconds(50)
  //       setDayVote(setDayVote((prev) => !prev))
  //       console.log(day);
  //     }
  //   }, 100)

  //   return () => clearInterval(timer)
  // }, [seconds])

  const soudBell = TaimeSound

  const soundPlay = (src) => {
    const sound = new Howl({
      src
    })
    sound.play()
  };

  const formatRemainingTime = (remainingTime) => {
    const minutes = Math.floor(remainingTime / 60)
    const seconds = remainingTime % 60
    return `${minutes}:${seconds}`
  }



  const renderNight = ({ remainingTime }) => {
    // if(remainingTime === 10){
    //   setDayVote(!dayVote)
    //   console.log(dayVote);
    // }
    if (remainingTime === 0) {
      setTimeout(() => {
        soundPlay(soudBell)
      }, 100);
      setNight(false)
      setDay(true)
      return <div className={style.timer}>!</div>;
    }

    return (
      <div className={style.timer}>
        <div className={style.textN}> :</div>
        <div className={style.value}>{remainingTime}</div>

      </div>
    );
  };

  const renderDay = ({ remainingTime }) => {

    if (remainingTime === 0) {
      setTimeout(() => {
        soundPlay(soudBell)
      }, 100);

      setDay(false)
      setNight(true)
      return <div className={style.timer}>!</div>;
    }

    return (
      <div className={style.timer}>
        <div className={style.textD}> :</div>
        <div className={style.value}>{formatRemainingTime(remainingTime)}</div>
      </div>
    );
  };

  return (
    <>
      {/* <h1>{seconds}</h1> */}
      <div className={style.wrapper}>
        {day && (
          <div className={style.timerWrapper}>
            <CountdownCircleTimer

              // initialRemainingTime={15} //     
              // updateInterval={0}  //   
              // size={180}          // 
              // strokeWidth={12}    //    
              // trailStrokeWidth={40}  //    
              // strokeLinecap={'round'} //   'round | square | butt'
              // isSmoothColorTransition={true} //   true/false
              // rotation={'clockwise'} //   'clockwise | counterclockwise'
              // children
              // onUpdate={(remainingTime: 1) => }

              isPlaying={true}             //    (true/false)
              duration={59}          // 
              colors={["#004777", "#F7B801", "#A30000", "#A30000"]}
              colorsTime={[230, 120, 60, 0]}
              onComplete={() => ({ shouldRepeat: true, delay: 5 })}
            >
              {renderDay}
            </CountdownCircleTimer>
          </div>
        )}
        {night && (
          <div className={style.timerWrapper}>
            <CountdownCircleTimer

              // updateInterval={0}  //   
              // size={180}          // 
              //  strokeWidth={12}    //    
              // trailStrokeWidth={40}  //    
              // strokeLinecap={'round'} //   'round | square | butt'
              // isSmoothColorTransition={true} //   true/false
              // rotation={'clockwise'} //   'clockwise | counterclockwise'
              // children
              // onUpdate={(remainingTime: 1) => }

              // initialRemainingTime={59} //     
              isPlaying={true}             //    (true/false)
              duration={25}          // 
              colors={["#004777", "#F7B801", "#A30000", "#A30000"]}
              colorsTime={[45, 25, 10, 0]}
              onComplete={() => ({ shouldRepeat: true, delay: 15 })}
            >
              {renderNight}
            </CountdownCircleTimer>
          </div>
        )}
      </div>
      <ModalVote night={night} />
    </>
  );
}

export default Taimer;
